.wp-block-create-block-toastmasters-dynamic-agenda {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}

.next-meeting-prompt {
	background-color: #fff;
	color: #7C2128;
	font-weight: bold;
}
.tm-notification {
	width: 600px;
	margin-left: auto;
	margin-right: auto;
	color: #fff;
	padding: 2px;
	animation-name: fadeOut;
	animation-delay: 20s;
	animation-duration: 5s;
	border: medium solid darkblue;
	z-index: 10;
}
.tm-notification-success {
	background-color: #21759b;
}
.tm-notification-error
{
	background-color:red;
}
@keyframes fadeOut {
	0% {opacity: 1;}
	100% {opacity: 0;}
}

button.tmform {
	display: inline-block;
	margin-right: 15px;
	padding: 10px;
	vertical-align: middle;
	background-color: #004165;
	color: #fff;
	font-weight: bold;
	border-radius: 10%;
	border: thin solid #444165;
}

button.tmform:hover {
	color: #004165;
	background-color: #F2DF74;
}

button.blockmove {
	display: inline-block;
	margin-right: 15px;
	padding: 10px;
	vertical-align: middle;
	background-color: #7C2128;
	color: #fff;
	font-weight: bold;
	border-radius: 10%;
	border: thin solid #444165;
}

button.blockmove:hover {
	color: #7C2128;
	background-color: #F2DF74;
}

.tmflexrow {
	display: flex;
	gap: 20px;
	align-items: top;
}
.tmflex30 {
	width: 30%;
}
//.tmflex50 {
	//width: 45%;
//}
.rb {
	padding: 5px;
	margin: 5px;
	border: medium solid #aaa;
	border-radius: 5px;
	background-color: #fff;
	color: #000;
}
.rb:hover {
	border: medium solid #000;
}
.radio-mode .components-flex {
	flex-flow: row;
}
#fixed-mode-control {
	position: fixed;
	bottom: 0;
	left: 0;
	padding: 15px;
	background-color: #fff;
	border-top: thick solid #7C2128;
	width: 100%;
	opacity: 1;
	z-index: 200;
}
#fixed-mode-control .radio-mode {
	width: 600px;
	margin-left: auto;
	margin-right: auto;
}
